
.albumImgTitle{
     position: relative;
     overflow: hidden; 
     padding: 30px 10px 30px 15px;
}
.albumImgTitle .cvr{
         position: relative;
         background-size: 145px;
             height: 145px;
             float: left;
}
.albumImgTitle .cnt{
     position: relative;
     margin-left: 180px;
}
.albumImgTitle .cnt h2{
     padding-top: 10px;
     font-size: 17px;
     line-height: 1.3;
     color: #fefefe;
     word-break: break-all;
     display: -webkit-box;
     -webkit-line-clamp: 2;
     -webkit-box-orient: vertical;
     overflow: hidden;
}
.albumImgTitle .cover {
     position: absolute;
     top: 0;
     left: 0;
}
.albumImgTitle .tag {
     position: absolute;
     z-index: 3;
     top: 10px;
     left: 0px;
     padding: 0 8px;
     height: 17px;
     color: #fff;
     font-size: 9px;
     text-align: center;
     line-height: 17px;
     background-color: rgba(217,48,48,.8);
     border-radius: 0 9999px 9999px 0;
}
.albumImgTitle .maskBox{
     position: absolute;
     top: 0;
     right: 0;
     bottom: 0;
     left: 0;
     background-color: rgba(0,0,0,0.25);
}
.albumImgTitle_bgimg{
     background-image: url(http://p4.music.126.net/nGeCOE1MUY6Kf7j7Le73bQ==/3420580690947051.jpg?param=177y177);
     background-size: cover;
     background-position: center;
     width: 100%;
     height: 100%;
     position: absolute;
     left: 0;
     top: 0;
     right: 0;
     bottom: 0;
     -webkit-filter: blur(20px);
     filter: blur(20px);
     -webkit-transform: scale(1.5);
     transform: scale(1.5);
}
.art{
        margin-top: 22px;
     color: rgba(255,255,255,1);
}
.s-fc0 {
     color: #fff;
}
.cnt .time{
     margin-top: 5px;
     color: rgba(255,255,255,0.5);
}



