
.selectDiv{
	display: none;
}
.selectDiv .weui-select{
	width: 105px;
    box-sizing: border-box;
	height: 28px;
	line-height: 28px;
	padding:0 15px;
}
.weui-search-bar_focusing .selectDiv{
    position: relative;
	display: block;
}
.selectDiv:before {
    content: " ";
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #c8c8cd;
    border-style: solid;
    -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
            transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: relative;
    top: -2px;
    position: absolute;
    top: 50%;
    right: 15px;
    margin-top: -4px;
}



