
.back-icon:before, .next-icon:before{
		font-size: 1.5rem;
    	color: inherit;
}
.icon{
		font-family:'icomoon' !important;
		font-size: 1.8rem;
    	color: inherit;
}
.icon[class^="icon-"],.icon[class*=" icon-"]{
		line-height: 40px;
}
.weui-tabbar.topStyle{
		top:0;
		bottom:	auto !important;
}
.topUlFlex{
		width: 100%;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
}
.topUlFlex li{
		padding: 5px 0 0;
		line-height: 40px;
}
.centerIcon{
		width:50%;
		text-align: center;
		height: 40px;
}



